@import '../../../variables';

.layer-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 140px;
  .layers {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    .layer {
      display: flex;
      flex-direction: column;
      align-items: center;
      .unselected-layer {
        border-width: 0;
      }
    }
    .option {
      color: $color-gray-600;
      &:hover {
        cursor: pointer;
        color: $primary;
      }
    }
  }
}

.keyboards {
  display: flex;
  flex-direction: row;
  justify-content: center;
  position: relative;
  z-index: 2;
  .keyboard-root {
    display: flex;
    justify-content: center;
    align-items: center;
    border-left: 5px solid #ececec;
    border-right: 5px solid #ececec;
    border-top: 1px solid #ececec;
    border-bottom: 8px solid #f3f3f3;
    border-radius: 8px;
    background-color: #fcfcfc;
    -webkit-filter: drop-shadow(0px 2px 2px rgba(0, 0, 0, 0.2));
    -moz-filter: drop-shadow(0px 2px 2px rgba(0, 0, 0, 0.2));
    -ms-filter: drop-shadow(0px 2px 2px rgba(0, 0, 0, 0.2));
    filter: drop-shadow(0px 2px 2px rgba(0, 0, 0, 0.2));
    .keyboard-frame {
      width: 100%;
      position: relative;
    }
  }
  .app-info {
    position: absolute;
    right: $space-l;
    bottom: $space-s;
    color: $primary-pale;
  }
}
